import './styles/QuizPage.css';
import Banner from "../components/Banner";
import {useNavigate, useParams} from "react-router";
import Quiz from "../components/Quiz";

export default function QuizPage() {
    const params = useParams();
    const navigation = useNavigate();
    const welcomeToQuiz = () => {
        return (
            <div className='quiz-page-welcome'>
                <div className='quiz-page-guide'>
                    <p>Vei primi un chestionar cu 26 de ntrebri aleatorii. Avei 30 de minute s rspundei corect la
                        minimum 22 dintre acestea pentru a promova testul de antrenament.</p>
                    <p>ntrebrile pot avea mai multe rspunsuri corecte i trebuie selectate toate acestea.</p>
                    <p>Fiind un test de antrenament, acesta va continua chiar dac nu mai poate fi promovat (daca avei
                        5 sau mai multe rspunsuri greite).</p>
                    <p>Putei sri peste o ntrebare pentru a rspunde la aceasta mai trziu.</p>
                    <h3>Succes!</h3>
                </div>
                <div className='quiz-page-start-button-container'>
                    <button className='quiz-page-start-button' onClick={() => navigation('/quiz/test')}>
                        START
                    </button>
                </div>
            </div>
        )
    }

    const displayContent = () => {
        if (!params.state) {
            return welcomeToQuiz();
        } else if (params.state === 'test') {
            return (
                <div className='test'>
                    <Quiz />
                    <div className='test-reset-button-container' onClick={() => window.location.reload() }>
                        <button className='test-reset-button'>Test nou</button>
                    </div>
                </div>
            );
        }
    }
    return (
        <div className='quiz-page'>
            <Banner
                title={'Chestionare Auto'}
            backButtonHandler={() => navigation('/')}/>
            {displayContent()}
        </div>
    )
}